<template>

  <div class="search">
    <form v-on:submit.prevent="postMovie">
	<input type="text" v-model="title">
  <input type="text" v-model="poster">
   <button  class= "btn btn-primary" >post</button>
</form>

    <div  class="header">
  
   <input type="text" v-model="searchkey">
   <button  class= "btn btn-primary" v-on:click="searchMovies">Search</button>
    
    </div>
	<h2>Result</h2>
<p>{{error}}</p>
	<div v-for="item in movieList" v-bind:key="item.imdbID" class="movieList">
    
			<div class="card" >
          <img  v-bind:src="item.Poster">
              <div >
              <h5 >{{item.Title}}</h5>
              <button round
                  @click="singleMovie(item.imdbID)"
                  >View</button>
      </div>
    </div>
</div>



	</div>
	

</template>
<script>
import axios from "axios"

export default {
  name: 'search',
  data () {
    return {
    	
      msg: 'search',
      searchkey:'',
      title:"",
      poster:"",
      movieList:[],
      error:""
    }
  },
  //  beforeMount(){
    
  //    var url='http://www.omdbapi.com/?apikey=f2d99df4&s=movie'; 
	// 		axios.get(url)
	// 		.then(response=>{
	// 			this.movieList=response.data.Search;
	// 		})
  //  },
  methods:{
  	searchMovies() 

		{  
      let movies=[]
     var url='http://www.omdbapi.com/?s=' + this.searchkey + '&apikey=f2d99df4'; 
     console.log(url)
			axios
      .get(url)
			.then(response=>{
      //console.log(response.data.Search)
        this.error=""
        if(response.data.Error){
          this.error=response.data.Error
        }
        
          //console.log(element)
          this.movieList=response.data.Search
        
			})
    },
    postMovie() 
		{  
			this.movieList.push({
        Title:this.title,
        Poster:this.poster
      }) 
        alert("movie added at the end")
		},
singleMovie (id) {
  console.log(id)
      this.$router.push('/movie/' + id)
    }
  }

}
</script>



<style >

.header
{
    height: 400px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5),rgba(5, 189, 221, 0.5)),url(../assets/background.jpg);
    background-size: cover;
    background-position:center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    
}
.header h1{
    color: #fff;
    padding:10px;
    border: none;
    border-radius: 25px;
    outline: none;
}

.column {
    float: left;
    width: 25%;
    padding: 0 10px;
  }
  
  /* Remove extra left and right margins, due to padding */
  .row {margin: 0 -5px;}
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Responsive columns */
  @media screen and (max-width: 600px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }
  
  /* Style the counter cards */
  .card {
    box-shadow: 0 4px 8px 0 rgba(11, 78, 75, 0.2);
    align-items: flex-end;
    padding: 16px;
    align-content: center;
    text-align: center;
    background-color:#f1f1f1;
    background-image: linear-gradient(rgba(0, 195, 255, 0.5),rgb(255, 255, 255));
   
  }
  .movieList{
     width: 20rem; text-align:center;display:inline-block;  
     margin: 20px;   
  }
  
  .card img {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  width: 300px;
  height: 300px;
}
</style>










