<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12>
        <h2>welcome to single movie component</h2>
        <img class="card-img-top" v-bind:src="singleMovie.Poster" alt="Card image cap">
          <div><p>title:{{singleMovie.Title}}</p></div>
          <div><p>Year:{{singleMovie.Year}}</p></div>
          <div><p>Rated:{{singleMovie.Rated}}</p></div>
          <div><p>Plot:{{singleMovie.Plot}}</p></div>
      </v-flex>
      <router-link to="/">search page</router-link>
    </v-layout>
  </v-container>
</template>
<script>
import axios from 'axios'
export default {
  props: ['id'],
  data () {
    return {
      singleMovie: ''
    }
  },

  mounted () {
      
    axios
      .get("http://www.omdbapi.com/?&apikey=f2d99df4&i="+this.id)
      .then(response => {
        this.singleMovie = response.data
      })
      .catch(error => {
        console.log(error)
      })
    
  }
}
</script>
<style>
</style>


