import Vue from 'vue'
import Router from 'vue-router'
import Search from '@/components/Search.vue'
import Movie from "@/components/Movie.vue"
Vue.use(Router)

export default new Router({
  // routes: [
  //   // {
  //   //   path: '/',
  //   //   name: 'HelloWorld',
  //   //   component: HelloWorld
  //   // }
     routes:[
    {
      path: '/',
      name: 'Search',
      component: Search
    },
      {
      path: '/movie/:id',
      name: 'Movie',
      props:true,
      component: Movie
    }


  ],
  mode:'history'

})



